import React from "react";
import { useLive, useMyList } from "../hooks/context";
import { ago } from "./leaf";
import { Avatar } from "antd";

export function UserCard({ user, size }) {
  return (
    <div className="flex flex-col justify-center items-center">
      <img
        className="h-16 w-16 rounded-full object-cover object-center"
        src={user.img_url}
        key={user.id}
        alt={user?.name}
        title={user?.name}
      />
      <b>{user?.name}</b>
      <div>title</div>
    </div>
  );
}
export function UserTag({ user }) {
  return (
    <div className="flex items-center gap-2">
      <img
        className="h-6 w-6 rounded-full object-cover object-center"
        src={user.img_url}
        key={user.id}
        alt={user?.name}
        title={user?.name}
      />
      <span>{user?.name}</span>
    </div>
  );
}

export const UserBy = ({ user, time }) => {
  if (user) {
    return (
      <div className="flex gap-2 items-center my-2">
        <UserAvatar user={user} />
        <div>
          <b>{user?.name}</b>
          <div className="text-xs">{ago(time)}</div>
        </div>
      </div>
    );
  }
};

export const UserAvatar = ({ user, className }) => {
  const { onlineUsers } = useLive();
  return (
    <Avatar
      className={`ring-2 ${
        onlineUsers.find((x) => x.userId == user.id)
          ? "ring-green-400"
          : "ring-gray-300"
      } ${className}`}
      src={user.img_url}
      key={user.id}
      alt={user?.name}
      title={user?.name}
    />
  );
};
