import { TabBar } from "antd-mobile";
import {
  AppOutline,
  CalendarOutline,
  MessageOutline,
  UserOutline,
} from "antd-mobile-icons";
import React from "react";
import { GoogleLogin } from "@react-oauth/google";
import { useLocation, useNavigate } from "react-router-dom";
import { useCookies } from "react-cookie";
import { API_ROUTES } from "../const";
import axios from "axios";
import { useAuth } from "../hooks/context";
import { JSONTree } from "react-json-tree";
function AppNavBar(props) {
  const navigate = useNavigate();
  const location = useLocation();
  const pathname = location.pathname;
  const setRouteActive = (value) => {
    navigate(value);
  };
  const { userInfo, signIn } = useAuth();
  const tabs = (user)=>[
    {
      path: "/event",
      title: "Event",
      icon: <CalendarOutline />,
      //   badge: Badge.dot,
    },
    {
      path: "/buzz",
      title: "Buzz",
      icon: <AppOutline />,
      badge: "5",
    },
    {
      path: "/chat",
      title: "Chat",
      icon: <MessageOutline />,
      badge: "5",
    },
    {
      path: "/profile",
      title: "Profile",
      icon: <img src={user.img_url} className={`w-6 h-6 rounded-full ${pathname == '/profile' && 'ring-2'}`}/>,
      badge: "5",
    },
  ];

  return !userInfo ? (
    <GoogleLogin
      onSuccess={(credentialResponse) => {
        signIn(credentialResponse.credential);
      }}
      onError={() => {
        console.log("Login Failed");
      }}
      useOneTap
    />
  ) : (
    <>
      <TabBar
        className="bg-white pt-2 "
        onChange={(value) => setRouteActive(value)}
        activeKey={pathname}
      >
        {tabs(userInfo).map((item) => (
          <TabBar.Item key={item.path} icon={item.icon} title={item.title} />
        ))}
      </TabBar>
    </>
  );
}

export default AppNavBar;
