// MyContext.js
import React, { createContext, useContext, useEffect, useState } from "react";
import useCacheAndFetch from "./useFetch";
import { FloatingBubble, Loading } from "antd-mobile";
import { useCookies } from "react-cookie";
import { API_ROUTES } from "../const";
import axios from "axios";
import connectSocket from "../socket";

const MyContext = createContext();
export const MyProvider = ({ children }) => {
  const {
    loading,
    error,
    data: general,
    cacheTime,
    refresh,
  } = useCacheAndFetch({
    name: "general",
    url: API_ROUTES.GENERAL,
    age: 0,
  });
  if (loading || !general) {
    return (
      <div className="h-[80vmin] flex justify-center items-center">
        <center>
          <Loading />
          <br />
          loading...
        </center>
      </div>
    );
  } else {
    return <MyContext.Provider value={general}>{children}</MyContext.Provider>;
  }
};
export const useMyList = () => useContext(MyContext);

const UseFullComponentContext = createContext();
export const UseFullComponentProvider = ({ children }) => {
  const [commentContentId, setCommentContentId] = useState(null);

  return (
    <UseFullComponentContext.Provider
      value={{ commentContentId, setCommentContentId }}
    >
      {children}
    </UseFullComponentContext.Provider>
  );
};
export const useMyComponent = () => useContext(UseFullComponentContext);

const AuthContext = createContext();
export const AuthProvider = ({ children }) => {
  const [cookies, setCookie, removeCookie] = useCookies(["user_token"]);
  const [loading, setLoading] = useState(false);
  const [userInfo, setUserInfo] = useState(null);

  const signOut = () => {
    window.location.reload();
    window.location.href = "/";
    removeCookie("user_token");
    setUserInfo(null);
  };
  const signIn = (credential) => {
    // console.log("signin");
    setLoading(true);
    axios({
      method: "post",
      url: API_ROUTES.SIGN_IN,
      data: { credential },
    })
      .then(function (response) {
        let expires = new Date();
        expires.setTime(expires.getTime() + response.data.exp * 1000);
        setCookie("user_token", response.data.token, { path: "/", expires });
        setUserInfo(response.data);
      })
      .catch(function (error) {
        console.log("sign in fail", error);
      })
      .finally(() => {
        setLoading(false);
      });
  };
  const me = (token) => {
    setLoading(true);
    axios({
      method: "get",
      url: API_ROUTES.ME,
      headers: {
        authorization: `Bearer ${token}`,
      },
    })
      .then(function (response) {
        setUserInfo(response.data);
      })
      .catch(function (error) {
        console.log("sign in fail", error);
      })
      .finally(() => {
        setLoading(false);
      });
  };
  useEffect(() => {
    if (!userInfo && cookies.user_token) {
      me(cookies.user_token);
    }
    // console.log({ user_token: cookies.user_token });
  }, [cookies.user_token, userInfo]);
  if (loading) {
    return (
      <div className="h-[80vmin] flex justify-center items-center">
        <center>
          <Loading />
          <br />
          Checking Auth
        </center>
      </div>
    );
  } else {
    return (
      <AuthContext.Provider value={{ userInfo, signIn, signOut }}>
        {children}
      </AuthContext.Provider>
    );
  }
};
export const useAuth = () => useContext(AuthContext);

const Socket = createContext(); // Rename the context variable
export const SocketProvider = ({ children }) => {
  const [onlineUsers, setOnlineUsers] = useState([]);
  const [newMmesage, setNewMessage] = useState(null);
  useEffect(() => {
    async function users() {
      const socket = await connectSocket();
      socket.connect();
      socket.on("onlineUsers", (onlineUsers) => {
        setOnlineUsers(onlineUsers);
        console.log("Online Users:", onlineUsers);
      });
      socket.on("message", (message) => {
        setNewMessage(message)
      });
      socket.on("message", (message) => {
        setNewMessage(message)
      });
    }
    users();
  }, []);

  return <Socket.Provider value={{ onlineUsers, newMmesage, setNewMessage }}>{children}</Socket.Provider>;
};

export const useLive = () => useContext(Socket);
