import React from 'react';
import useCacheAndFetch from '../hooks/useFetch';
import { JSONTree } from 'react-json-tree';
import { Avatar, Card, Image, List, Loading, PullToRefresh, Space } from 'antd-mobile';
import moment from 'moment/moment';
import { CheckCircleOutline, HeartOutline, LockOutline, MessageOutline, RightOutline } from 'antd-mobile-icons';
import { useAuth, signOut, useMyList, useLive } from '../hooks/context';
import ErrorPage from './error-page';
import { json } from 'react-router-dom';
import { UserAvatar } from '../components/user';
import { Button } from 'antd';

function Profile(props) {
    const { userInfo, signOut } = useAuth();
    const mylist = useMyList()
    const { onlineUsers } = useLive();
    return <div>
        <div className=' sticky top-0 bg-white z-10 shadow-md px-4'>
            <div className='flex items-center gap-2 py-4'>
                <UserAvatar user={userInfo} />
                <h2>{userInfo.name}</h2>
                <div className='flex-1' />
                <Button icon={<LockOutline />} onClick={signOut} />
            </div>
            <hr />
            {/* <JSONTree data={mylist}/> */}
            {onlineUsers.length} {onlineUsers.length > 1 ? 'users' : 'user'} online.
        </div>
        <div className='px-4'>
            {mylist.user && mylist.user.map((u) => {
                return <UserListItem key={u.id} user={u} />
            })}
        </div>
    </div>
}

export default Profile;

function UserListItem({ user }) {
    return (
        <div className='flex items-center gap-2 my-4'>
            <UserAvatar user={user} />
            <h2>{user.name}</h2>
        </div>
    )
}