var development = false;
const host = development
  ? "http://localhost:3001"
  : "https://bdata-api.rayhub.online";
export const APP_API_URL = host;
export const API_ROUTES = {
  GENERAL: `${APP_API_URL}`,
  APP_API_URL: APP_API_URL,
  SIGN_IN: `${APP_API_URL}/auth/signin`,
  ME: `${APP_API_URL}/auth/me`,
  CONTENT: `${APP_API_URL}/contents`,
  CONTENT_CREATE: `${APP_API_URL}/content`,
  CONTENT_TYPE: ({ type_name }) => `${APP_API_URL}/contents/${type_name}`,
  CONTENT_DETAIL: ({ id }) => `${APP_API_URL}/content/${id}`,
  VOTE: ({ id }) => `${APP_API_URL}/content/vote/${id}`,
  COMMENT: ({ content_id }) => `${APP_API_URL}/content/${content_id}/comments`,
  MESSAGE: `${APP_API_URL}/messages`,
};
export const APP_ROUTES = {
  CONTENT: `/content`,
  CONTENT_DETAIL: ({ id }) => `/content/${id}`,
  CONTENT_CREATE: `/content/create`,
};
