import { useEffect, useState } from "react";
import useForm from "../hooks/useForm";
import { Button, FloatingBubble } from "antd-mobile";
import {
  AddOutline,
  CheckCircleFill,
  CheckCircleOutline,
  HeartFill,
  HeartOutline,
  MessageOutline,
} from "antd-mobile-icons";
import { API_ROUTES, APP_ROUTES } from "../const";
import { useAuth, useMyComponent, useMyList } from "../hooks/context";
import { useNavigate } from "react-router-dom";
import { Avatar, Space } from "antd";
import ContentFormPanel from "./ContentCreate";
import moment from "moment";
import { UserAvatar } from "./user";

export function VoteBtn({ content_id, vote_count, amIVote }) {
  const [vote, setVote] = useState(amIVote);

  // useForm hook for handling API requests
  const { loading, error, setMethod, submit, response } = useForm({
    target: API_ROUTES.VOTE({ id: content_id }),
  });

  // Update local storage based on API response
  useEffect(() => {
    var myVote = JSON.parse(localStorage.getItem("myVote")) || [];

    if (!loading && response) {
      if (!vote) {
        console.log("add");
        localStorage.setItem("myVote", JSON.stringify([...myVote, content_id]));
      } else {
        console.log("remove");
        myVote = myVote.filter((item) => item !== content_id);
        localStorage.setItem("myVote", JSON.stringify(myVote));
      }

      setVote(!vote);
    }
  }, [response, loading]);

  // Set the HTTP method based on the voting status
  useEffect(() => {
    if (!vote) {
      setMethod("post");
    } else {
      setMethod("delete");
    }
  }, [vote]);

  return (
    <Button color="primary" fill="none" shape="rounded" onClick={submit}>
      <div className="flex items-center">
        {vote ? (
          <HeartFill className={loading && "heartBeat"} />
        ) : (
          <HeartOutline className={loading && "heartBeat"} />
        )}
        <span className="text-xs">{vote_count || ""}</span>
      </div>
    </Button>
  );
}

export function SaveBtn({ content_id }) {
  var mySave = JSON.parse(localStorage.getItem("mySave")) || [];

  const [isSave, setIsSave] = useState(mySave.includes(content_id));

  const save = () => {
    var mySave_ = JSON.parse(localStorage.getItem("mySave")) || [];
    if (!isSave) {
      localStorage.setItem("mySave", JSON.stringify([...mySave_, content_id]));
      setIsSave(true);
    } else {
      mySave_ = mySave_.filter((item) => item !== content_id);
      localStorage.setItem("mySave", JSON.stringify(mySave_));
      setIsSave(false);
    }
  };

  return (
    <Button color="primary" fill="none" shape="rounded" onClick={save}>
      {isSave ? <CheckCircleFill /> : <CheckCircleOutline />}
    </Button>
  );
}

export function ContentCreateButton() {
  const [visible, setVisible] = useState(false);
  const { userInfo: user } = useAuth();
  return (
    user && (
      <>
        <ContentFormPanel visible={visible} setVisible={setVisible} />
        <div
          onClick={() => setVisible(true)}
          className="flex py-6 sticky top-0 z-10 gap-2"
        >
          <UserAvatar user={user} className="shadow-md"/>
          <div className="h-8 flex-1 bg-white shadow-md rounded-full flex items-center px-4 text-gray-500 ring-1 ring-gray-300">
            ဇာပြောချင့်ပါလေ
          </div>
        </div>
      </>
    )
  );
}

export function ContentActionBar({ id, vote_count, amIVote }) {
  const { setCommentContentId } = useMyComponent();
  return (
    <div className="p-2">
      <Space style={{ width: "100%", justifyContent: "space-between" }}>
        <VoteBtn content_id={id} vote_count={vote_count} amIVote={amIVote} />
        <Button
          color="primary"
          fill="none"
          shape="rounded"
          onClick={() => setCommentContentId(id)}
        >
          <MessageOutline />
        </Button>
        <SaveBtn content_id={id} />
      </Space>
    </div>
  );
}

export function CommentItem(comment) {
  const mylist = useMyList();
  var user = mylist?.user.find((x) => x.id == comment.user_id);
  if (user)
    return (
      <div className={`m-3 flex gap-2 `}>
        <UserAvatar
          user={user}
        />
        <div className="ms-1">
          <span className="font-bold">{user?.name}</span>
          <div
            className={`bg-white shadow-lg border p-2 px-3  rounded-se-xl rounded-ee-xl rounded-es-xl min-w-[100px]`}
          >
            {comment.comment}
            <div className={`flex justify-end text-xs opacity-50`}>
              <i>{ago(comment.create_time)}</i>
            </div>
          </div>
        </div>
        <div className="p-4 w-32"></div>
      </div>
    );
}

export function ago(dateTime) {
  const now = moment();
  const targetTime = moment(dateTime);

  const diffInMinutes = now.diff(targetTime, "minutes");
  const diffInHours = now.diff(targetTime, "hours");
  const diffInDays = now.diff(targetTime, "days");
  const diffInYears = now.diff(targetTime, "years");

  if (diffInMinutes < 1) {
    return "just now";
  } else if (diffInHours < 1) {
    return `${diffInMinutes} min`;
  } else if (diffInDays < 1) {
    return `${diffInHours} hr`;
  } else if (diffInDays === 1) {
    return "1 day";
  } else if (diffInYears < 1) {
    return `${diffInDays} day`;
  } else if (diffInYears === 1) {
    return "1 year";
  } else {
    return `${diffInYears} years`;
  }
}
