import React, { useEffect, useMemo, useState } from "react";
import { Select, Space } from "antd";
import { useMyList } from "../../hooks/context";
import { UserTag } from "../user";
import { DatePicker } from "antd-mobile";
import moment from "moment";
import { JSONTree } from "react-json-tree";

const filterOption = (input, option) =>
  (option?.label ?? "").toLowerCase().includes(input.toLowerCase());

export const InputMultiUser = ({ onChange, defaultValue, value }) => {
  const mylist = useMyList();
  useEffect(() => {
    if (defaultValue) {
      onChange(defaultValue);
    }
  }, []);
  const options = useMemo(() => {
    if (mylist.user) {
      console.log(mylist.user);
      return mylist.user.map((u) => {
        return {
          label: <UserTag user={u} />,
          value: u.id,
        };
      });
    } else {
      return [];
    }
  }, [mylist.user]);

  return (
    <>
    {/* <JSONTree data={value}/> */}
    <Select
      size="large"
      mode="multiple"
      style={{
        width: "100%",
      }}
      placeholder="select one speaker"
      value={value}
      onChange={onChange}
      optionLabelProp="label"
      options={options}
    />
    </>
  );
};
export const InputTopic = ({ onChange, defaultValue, value }) => {
  const mylist = useMyList();
  useEffect(() => {
    if (defaultValue) {
      onChange(defaultValue);
    }
  }, []);
  return (
    <>
    {/* <JSONTree data={{value,defaultValue}}/> */}
    <Select
      showSearch
      mode="multiple"
      style={{
        width: "100%",
      }}
      value={value}
      defaultValue={defaultValue}
      placeholder="Select a Topic"
      optionFilterProp="children"
      onChange={onChange}
      filterOption={filterOption}
      options={mylist.topic.map((t) => {
        return {
          value: t.id,
          label: t.name,
        };
      })}
    />
    </>
  );
};
export const InputTypeId = ({ onChange, value, render }) => {
  const mylist = useMyList();
  return (
    <Select
      showSearch
      value={value}
      placeholder="Select a Type"
      optionFilterProp="children"
      onChange={onChange}
      filterOption={filterOption}
      options={mylist.type.map((t) => {
        return {
          value: t.id,
          label: render ? render(t) : t.name,
        };
      })}
    />
  );
};

export const InputeDate = ({ onChange, value, defaultValue }) => {
  const now = new Date();
  const [datePicker, setDatePicker] = useState(false);
  useEffect(() => {
    if (defaultValue) {
      onChange(defaultValue);
    }
  }, []);
  return (
    <>
      <DatePicker
        title="Event Time"
        cancelText="Cancel"
        confirmText="Ok"
        visible={datePicker}
        precision="minute"
        onClose={() => {
          setDatePicker(false);
        }}
        min={now}
        defaultValue={now}
        onConfirm={onChange}
      />
      <div className="ms-2 mb-2 text-center">
        <div
          onClick={() => setDatePicker(true)}
          className="cursor-pointer  p-2 rounded-md shadow-2xl bg-white"
        >
          <div>{moment(value).format("MMM")}</div>
          <div className="leading-none text-3xl font-bold">
            {moment(value).format("DD")}
          </div>
          <div className="text-xs opacity-60">
            {moment(value).format("YYYY")}
          </div>
          <hr className="my-2" />
          <div>{moment(value).format("hh:mm a")}</div>
        </div>
      </div>
    </>
  );
};
