import { useEffect, useState } from "react";
import axios from "axios";
import { useCookies } from "react-cookie";

export default function useCacheAndFetch({ name: cacheKey, url, age = 1440 }) {
  //age must be minute - undefine is 1440 min /24 hour - 0 is no cache
  const [page, setPage] = useState(1);
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [cacheTime, setCacheTime] = useState(null);
  const [hasMore, setHasMore] = useState(true);

  const [cookies] = useCookies(["user_token"]);

  const getSingleCacheData = async ({ cacheKey, url }) => {
    if (typeof caches === "undefined") {
      return null;
    } else {
      const cache = await caches.open(cacheKey);
      const cachedResponse = await cache.match(url);

      // If cache exists
      if (cachedResponse && cachedResponse.ok) {
        return cachedResponse.json().then((item) => {
          return item;
        });
      } else {
        return null;
      }
    }
  };

  const addDataIntoCache = (cacheKey, url, _cacheData) => {
    const cacheData = {
      timeCached: Date.now(),
      data: _cacheData,
    };
    const data = new Response(JSON.stringify(cacheData));
    if ("caches" in window) {
      caches.open(cacheKey).then((cache) => {
        cache.put(url, data);
      });
    }
  };

  function deleteCache({ cacheKey, url }, all = null) {
    if (typeof caches !== "undefined") {
      if (all) {
        caches.delete(all);
      } else {
        caches.open(cacheKey).then((cache) => {
          cache.delete(url);
        });
      }
    }
  }

  async function loadMore() {
    if (!loading) {
      setLoading(true);
      setError(null);
      var theurl = `${url}?page=${page + 1}`;
      const cacheData = await getSingleCacheData({ cacheKey, url: theurl });
      // console.log({ cacheKey, theurl });
      if (cacheData) {
        // console.log(cacheData.data);

        setData((old) => {
          return [...(old || []), ...cacheData.data];
        });
        setPage((old) => old + 1);
        setLoading(false);
      } else {
        axios({
          method: "get",
          url: theurl,
          headers: {
            authorization: `Bearer ${cookies.user_token}`,
          },
        })
          .then(function (response) {
            setData((old) => {
              return [...(old || []), ...response.data];
            });
            setPage((old) => old + 1);
            addDataIntoCache(cacheKey, theurl, response.data);
          })
          .catch(function (error) {
            console.error(error);
            // setError(error);
            setHasMore(false);
          })
          .finally(() => {
            setLoading(false);
          });
      }
    }
  }
  function fatch(refresh) {
    //sent server word
    setLoading(true);
    setError(null);
    axios({
      method: "get",
      url: url,
      headers: {
        authorization: `Bearer ${cookies.user_token}`,
      },
    })
      .then(function (response) {
        setData(response.data);
        setHasMore(true);
        setPage(1);
        if (refresh) {
          deleteCache({ cacheKey, url });
          const currentTime = Date.now();
          setCacheTime(currentTime);
        }
        addDataIntoCache(cacheKey, url, response.data);
      })
      .catch(function (error) {
        console.error(error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }
  function refresh() {
    fatch(true);
  }
  useEffect(() => {
    if (url) {
      setError(null);
      setData(null);
      const go = async () => {
        const cacheData = await getSingleCacheData({ cacheKey, url });
        if (cacheData) {
          // console.log(cacheData)
          const cacheTime = cacheData.timeCached;
          const currentTime = Date.now();
          setCacheTime(cacheTime);
          if (currentTime - cacheTime > age * 60000) {
            fatch(true);
          } else {
            setData(cacheData.data);
          }
        } else {
          fatch();
        }
      };
      go();
    }
  }, [url]);

  const addData = (d, end) => {
    if (Array.isArray(data)) {
      if (end) {
        setData((oldd) => {
          return Array.isArray(oldd) ? [...oldd, d] : [d];
        });
      } else {
        setData((oldd) => {
          return Array.isArray(oldd) ? [d, ...oldd] : [d];
        });
      }
    } else {
      setData([d]);
    }
  };

  const removeData = (i) => {
    setData((old) => old.splice(i, 1));
  };

  const updateData = ({ data: updatedData, index }) => {
    var newData = [...data];
    newData[index] = updatedData;
    setData(newData);
  };

  return {
    loading,
    error,
    data,
    refresh,
    addData,
    removeData,
    updateData,
    cacheTime,
    loadMore,
    hasMore,
    deleteCache,
  };
}
