import {
  Avatar,
  Button,
  DotLoading,
  Image,
  List,
  Loading,
  Space,
} from "antd-mobile";
import {
  CheckCircleOutline,
  HeartFill,
  HeartOutline,
  MessageOutline,
} from "antd-mobile-icons";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, json } from "react-router-dom";
import { useMyList } from "../../hooks/context";
import { JSONTree } from "react-json-tree";
import useForm from "../../hooks/useForm";
import { API_ROUTES, APP_ROUTES } from "../../const";
import { Flex } from "antd";
import { ContentActionBar, SaveBtn, VoteBtn } from "../leaf";
import EventCard from "./event";

function ContentCard(props) {
  const [noImg, setNoImg] = useState(false);
  switch (props.type) {
    case "event":
      return <EventCard {...props} />;
    default:
      const {
        id,
        img_url,
        fields,
        title,
        description,
        topics,
        vote_count,
        amIVote,
      } = props;
      return (
        <div className="bg-white rounded-xl max-w-md shadow-sm overflow-hidden select-none">
          <Link to={APP_ROUTES.CONTENT_DETAIL({ id })} className="text-inherit">
            {/* <JSONTree data={mylist.user}/> */}
            <ContentListItem
              {...{
                img_url,
                fields,
                title,
                description,
                topics,
              }}
            />
            {!noImg && img_url && (
              <Image
                style={{ width: "100%" }}
                src={img_url}
                onError={setNoImg}
              />
            )}
          </Link>

          <ContentActionBar {...{ id, vote_count, amIVote }} />
        </div>
      );
  }
}

export default ContentCard;

export function ContentListItem({ title, description, fields, topics }) {
  return (
    <>
      {/* {img_url} */}
      <div className="flex gap-3 p-4">
        <div className="flex-1">
          <div className="flex justify-between">
            <div className="text-lg">{title}</div>
          </div>
          <div className="opacity-70 line-clamp-2">
            {description}
          </div>
          <i>{topics.map((t, i) => `#${t.name}`).join(", ")} </i>
        </div>
      </div>
    </>
  );
}
