import { Button, DotLoading, Image, List, Loading, Space } from "antd-mobile";
import {
  CheckCircleOutline,
  HeartFill,
  HeartOutline,
  MessageOutline,
} from "antd-mobile-icons";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { Link, json } from "react-router-dom";
import { useMyList } from "../../hooks/context";
import { JSONTree } from "react-json-tree";
import useForm from "../../hooks/useForm";
import { API_ROUTES, APP_ROUTES } from "../../const";
import { Avatar, Flex } from "antd";
import { ContentActionBar, SaveBtn, VoteBtn } from "../leaf";
import { UserAvatar } from "../user";

function EventCard({
  id,
  img_url,
  fields,
  title,
  description,
  topics,
  speaker_ids,
  vote_count,
  amIVote,
}) {
  const [noImg, setNoImg] = useState(false);
  return (
    <div className="bg-white rounded-xl max-w-md shadow-sm overflow-hidden select-none">
      <Link to={APP_ROUTES.CONTENT_DETAIL({ id })} className="text-inherit">
        {!noImg && img_url && (
          <Image style={{ width: "100%" }} src={img_url} onError={setNoImg} />
        )}

        {/* <JSONTree data={mylist.user}/> */}
        <EventListItem
          {...{
            img_url,
            fields,
            title,
            description,
            topics,
            speaker_ids,
          }}
        />
      </Link>

      <ContentActionBar {...{ id, vote_count, amIVote }} />
    </div>
  );
}

export default EventCard;

export function EventListItem({ title, description, fields, topics }) {
  const mylist = useMyList();
  return (
    <>
      {/* {img_url} */}
      <div className="flex gap-3 p-4">
        <div className="flex-1">
          <div className="flex justify-between">
            <div className="text-lg">{title}</div>
          </div>
          <div className="opacity-70 line-clamp-2">{description}</div>
          <i>{topics.map((t, i) => `#${t.name}`).join(", ")} </i>
          {fields.speaker_ids && (
            <div className="flex gap-2">
              {mylist.user
                .filter((u) =>
                  fields.speaker_ids.split(",").includes(String(u.id))
                )
                .map((user) => {
                  return <UserAvatar key={user.id} user={user} />;
                })}
            </div>
          )}
        </div>

        {fields.event_date && (
          <div className="text-center p-2 rounded-md shadow-2xl bg-white">
            <div>{moment(fields.event_date).format("MMM")}</div>
            <div className="leading-none text-3xl font-bold">
              {moment(fields.event_date).format("DD")}
            </div>
            <div className="text-xs opacity-60">
              {moment(fields.event_date).format("YYYY")}
            </div>
            <hr className="my-2" />
            <div>{moment(fields.event_date).format("hh:MM a")}</div>
          </div>
        )}
      </div>
    </>
  );
}
