import { Space } from "antd-mobile";
import React from "react";
import { useAuth, useMyList } from "../hooks/context";
import { ago } from "./leaf";
import { Avatar } from "antd";
import { UserAvatar } from "./user";

export function MessageItem({ id,message, user_id, create_time , className}) {
  const { userInfo } = useAuth();
  const { user: userList } = useMyList();
  var isMe = userInfo?.id == user_id;
  var user = userList.find((u) => (u.id == user_id));
  return (
    <div className={`m-3 flex gap-2 ${isMe ? "flex-row-reverse" : ""} ${className}`}>
      {!isMe && <UserAvatar user={user} />}
      <div className="ms-1">
        {!isMe && <span className="font-bold">{user.name}</span>}
        <div
          className={`bg-white shadow-lg border p-2 px-3 rounded-se-xl ${
            isMe && "rounded-ss-xl"
          } rounded-ee-xl rounded-es-xl`}
        >
          {/* {id} */}
          {message}
          <div
            className={`${isMe ? "" : "flex justify-end"} text-xs opacity-50`}
          >
            <i>{ago(create_time)}</i>
          </div>
        </div>
      </div>
      <div className="p-4 w-32"></div>
    </div>
  );
}
