import React, { useEffect, useRef, useState } from "react";
import useCacheAndFetch from "../hooks/useFetch";
import { JSONTree } from "react-json-tree";
import { Image, Input, List, Loading, PullToRefresh, Space } from "antd-mobile";
import moment from "moment/moment";
import {
  CheckCircleOutline,
  HeartOutline,
  MessageOutline,
  RightOutline,
  SendOutline,
} from "antd-mobile-icons";
import EventCard from "../components/content/event";
import { MessageItem } from "../components/chat";
import { IcoSend } from "../components/Icon";
import { API_ROUTES } from "../const";
import useForm from "../hooks/useForm";
import { Button, message } from "antd";
import { useLive, useMyList } from "../hooks/context";
import { useCookies } from "react-cookie";
import axios from "axios";
import ScrollContainer from "./tast";
import InfiniteScroll from "react-infinite-scroll-component";

export default function Chat(props) {
  const [cookies] = useCookies(["user_token"]);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [error, setError] = useState(null);
  const [messages, setMessages] = useState([]);
  const [lastFetchData, setLastFetchData] = useState(null);
  const [oldestId, setOldestId] = useState(null);
  const { newMmesage, onlineUsers: online } = useLive();
  const mylist = useMyList();
  var onlineUsers = mylist.user.filter((u) =>
    online.map((x) => x.userId).includes(u.id)
  );
  // MESSAGE
  // const {
  //   loading,
  //   error,
  //   data: messages,
  // } = useCacheAndFetch({
  //   name: "Comment",
  //   url: API_ROUTES.MESSAGE,
  //   age: 0,
  // });

  const load = (queryParams) => {
    setLoading(true);
    axios({
      method: "get",
      url: API_ROUTES.MESSAGE,
      headers: {
        authorization: `Bearer ${cookies.user_token}`,
      },
      params: queryParams,
    })
      .then(function (response) {
        setLastFetchData(response.data);
      })
      .catch(function (error) {
        console.error(error);
        setHasMore(false);
      })
      .finally(() => {
        setLoading(false);
      });
  };
  const messagesEndRef = useRef(null);

  const scrollToBottom = (smooth) => {
    setTimeout(() => {
      messagesEndRef.current?.scrollIntoView(smooth && { behavior: "smooth" });
    }, 10);
  };
  useEffect(() => {
    load();
    scrollToBottom();
  }, []);
  const loadMore = () => {
    if (!loading) {
      load({
        theId: oldestId,
        how: "before",
      });
    }
  };
  useEffect(() => {
    if (lastFetchData) {
      const smallestId = Math.min(...lastFetchData.map((item) => item.id));
      setOldestId(smallestId);
      // lastFetchData.sort((a, b) => {
      //   const dateA = moment(a.create_time);
      //   const dateB = moment(b.create_time);

      //   // Compare moments
      //   if (dateA.isBefore(dateB)) {
      //     return -1; // a should come before b
      //   } else if (dateA.isAfter(dateB)) {
      //     return 1; // a should come after b
      //   } else {
      //     return 0; // dates are equal
      //   }
      // });

      setMessages((old) => {
        return [...(old || []), ...lastFetchData];
      });
    }
  }, [lastFetchData]);

  // useEffect(() => {
  //   scrollToBottom();
  // }, [loading]);
  const statusRecord = {
    pulling: "ဆွဲ",
    canRelease: "လွှတ်",
    refreshing: "refreshing...",
    complete: "loading",
  };

  const {
    loading: senting,
    error: sendingErr,
    setBody,
    body,
    submit,
    response: messageSent,
  } = useForm({
    target: API_ROUTES.MESSAGE,
  });

  const handleSend = (event) => {
    event.preventDefault();
    submit();
  };
  useEffect(() => {
    if (messageSent) {
      setBody(null);
    }
  }, [messageSent]);
  useEffect(() => {
    if (newMmesage && messages.find((x) => x.id != newMmesage.id)) {
      scrollToBottom(true);
      setMessages((old) => {
        return [newMmesage, ...(old || [])];
      });
    }
  }, [newMmesage]);

  return (
    <div>
      <div className="sticky top-0 right-0 left-0 min-w z-10 backdrop-blur-md bg-gray-50/60">
        <div className="flex p-2">
          {onlineUsers.map((u) => {
            return (
              <img
                src={u.img_url}
                title={u.name}
                className="h-6 w-6 rounded-full"
              />
            );
          })}
        </div>

        {/* {hasMore && !loading && <Button onClick={loadMore}>Load more</Button>} */}
      </div>
      <InfiniteScroll
        dataLength={messages.length}
        next={loadMore}
        style={{ display: "flex", flexDirection: "column-reverse" }} //To put endMessage and loader to the top.
        inverse={true} //
        hasMore={hasMore}
        loader={<center><Loading/></center>}
        scrollableTarget="scrollableDiv"
        height={'calc(100vh - 10px)'}
        endMessage={<center>no more</center>}
        refreshFunction={loadMore}
      >
        {messages &&
          messages.map((m) => {
            return (
              <div key={m.id} className="my-2">
                <MessageItem
                  {...m}
                  className={newMmesage?.id == m.id && "newMessage"}
                />
              </div>
            );
          })}
      </InfiniteScroll>
      <div ref={messagesEndRef} className="h-20" />
      <div className="fixed right-0 left-0 bottom-14 min-w">
        <form
          onSubmit={handleSend}
          className="flex gap-2 items-center p-2 backdrop-blur-md bg-gray-50/60"
        >
          {/* <JSONTree data={body}/> */}
          <div className="bg-white rounded-full p-1 px-4 flex-1">
            <Input
              placeholder="ဇာပြောချင့်ပါလေ"
              className="rounded-full"
              value={body ? body.message : ""}
              onChange={(val) => setBody({ message: val })}
            />
          </div>
          <Button
            type={"submit"}
            className="bg-white"
            shape="circle"
            icon={<IcoSend />}
            onClick={handleSend}
          />
        </form>
      </div>
    </div>
  );
}
