import React, { useEffect, useRef, useState } from "react";
import { JSONTree } from "react-json-tree";
import {
  DatePicker,
  Image,
  Input,
  Modal,
  NavBar,
  Popup,
  Space,
  TextArea,
} from "antd-mobile";
import moment from "moment/moment";
import { useMyList } from "../hooks/context";
import { UserCard } from "./user";
import { CheckCircleOutline, ExclamationCircleFill } from "antd-mobile-icons";
import useForm from "../hooks/useForm";
import { API_ROUTES, APP_ROUTES } from "../const";
import { useNavigate } from "react-router-dom";
import {
  InputMultiUser,
  InputTopic,
  InputTypeId,
  InputeDate,
} from "./inputs/user";
import { Button, Select } from "antd";

export default function ContentFormPanel({
  visible,
  setVisible,
  defaultData,
  refresh,
}) {
  const [img, setImg] = useState(null);
  const mylist = useMyList();

  const navigate = useNavigate();

  const {
    loading,
    error,
    body: content,
    setBody,
    setMethod,
    submit,
    response,
  } = useForm({ target: API_ROUTES.CONTENT_CREATE });
  useEffect(() => {
    if (defaultData) {
      setBody(defaultData);
      if (defaultData.id) {
        setMethod("PUT");
      }
    } else {
      setMethod("POST");
    }
  }, [defaultData]);
  const onChange = (key, value) => {
    setBody((old) => {
      return { ...old, [key]: value };
    });
  };
  const fieldChange = (key, value) => {
    setBody((old) => {
      old = old || {};
      return { ...old, fields: { ...old.fields, [key]: value } };
    });
  };
  useEffect(() => {
    if (response) {
      setVisible(null);
      refresh();
      navigate(APP_ROUTES.CONTENT_DETAIL({ id: response.id }));
    }
    if (error) {
      Modal.show({
        closeOnAction: true,
        header: (
          <ExclamationCircleFill
            style={{
              fontSize: 64,
              color: "var(--adm-color-warning)",
            }}
          />
        ),
        title: "Opps!",
        content: error.response?.message
          ? error.response?.message
          : error.message,
        actions: [
          {
            key: "close",
            text: "Close",
            primary: true,
          },
        ],
      });
    }
  }, [response, error]);
  const [type, setType] = useState(null);
  const fields = mylist?.field?.filter((x) => x.type_id == type?.id);

  useEffect(() => {
    if (content?.type_id) {
      setType(mylist.type.find((x) => x.id == content.type_id));
    } else {
      setType(mylist.type[0]);
      onChange("type_id", mylist.type[0].id);
    }
  }, [content?.type_id]);

  return (
    <Popup
      visible={visible}
      onMaskClick={() => {
        // setVisible1(false);
      }}
      onClose={() => {
        setVisible(false);
      }}
      bodyStyle={{ height: "100vh", overflow: "auto" }}
    >
      {visible && (
        <div className="bg-white">
          {/* <JSONTree data={content} /> */}
          {/* <JSONTree data={defaultData}/>   */}
          <form
            onSubmit={(e) => {
              e.preventDefault();
              submit();
            }}
          >
            <NavBar
              onBack={() => setVisible(false)} //</form>navigate(-1)}
              className="shadow-lg sticky top-0 z-10 bg-white"
              right={
                <Button
                  type="submit"
                  shape="rectangular"
                  color="primary"
                  loading={loading}
                  onClick={submit}
                  loadingText="saving"
                >
                  <Space className="items-center">
                    Save
                    <CheckCircleOutline />
                  </Space>
                </Button>
              }
            >
              <div>{content?.id ? "Update Content" : "Create New Content"}</div>
              <InputTypeId
                value={content?.type_id}
                onChange={(val) => onChange("type_id", val)}
                render={(type) => `${type.name} type`}
              />{" "}
            </NavBar>
            <Image style={{ width: "100%" }} src={img} />
            <div className="m-4">
              <div>
                <div className="flex-1">
                  <div className="text-lg">
                    <Input
                      placeholder={`${type?.name} title`}
                      value={content?.title}
                      onChange={(val) => {
                        onChange("title", val);
                      }}
                    />
                  </div>
                  <div className="my-4">
                    <h2>Topic</h2>
                    <InputTopic
                      defaultValue={content?.topics?.map((x) => x.id)}
                      value={content?.topic_ids}
                      onChange={(val) => onChange("topic_ids", val)}
                    />
                  </div>
                </div>
                {fields.map((f) => {
                  switch (f.dataType) {
                    case "datetime":
                      return (
                        <div className="my-4">
                          <h2>{f.label}</h2>
                          <InputeDate
                            // defaultValue={new Date()}
                            value={content?.fields?.[f.name] || new Date()}
                            onChange={(val) => fieldChange(f.name, val)}
                          />
                        </div>
                      );
                    case "user_ids":
                      return (
                        <div className="my-4">
                          <h2>{f.label}</h2>
                          <InputMultiUser
                            // defaultValue={[2]}
                            value={JSON.parse(
                              `[${content?.fields?.[f.name] || ""}]`
                            )}
                            onChange={(val) =>
                              fieldChange("speaker_ids", val.join(","))
                            }
                          />
                        </div>
                      );
                    default:
                      break;
                  }
                })}
              </div>
              {/* <JSONTree data={content} /> */}
              <div className="my-4">
                <h3>Description</h3>
                <TextArea
                  placeholder="Description"
                  value={content?.description}
                  autoSize={{ minRows: 3 }}
                  onChange={(val) => onChange("description", val)}
                />
              </div>
              {/* <JSONTree data={content} /> */}
              {content?.user_ids && (
                <div className="flex gap-3 my-2">
                  {mylist.user
                    .filter((u) =>
                      content?.user_ids.split(",").includes(String(u.id))
                    )
                    .map((user) => {
                      return <UserCard user={user} key={user.id} />;
                    })}
                </div>
              )}
            </div>
          </form>
        </div>
      )}
    </Popup>
  );
}
