import React, { useEffect, useState } from "react";
import useCacheAndFetch from "../hooks/useFetch";
import { JSONTree } from "react-json-tree";
import {
  Avatar,
  Button,
  Card,
  Dialog,
  FloatingBubble,
  Image,
  List,
  Loading,
  Modal,
  PullToRefresh,
  Space,
} from "antd-mobile";
import moment from "moment/moment";
import { API_ROUTES, APP_ROUTES } from "../const";
import { useMyList } from "../hooks/context";
import InfiniteScroll from "../components/infiniteScroll";
import ContentCard from "../components/content";
import { ContentCreateButton, ago } from "../components/leaf";
import CommentPannel from "../components/CommentPannel";
import { UserBy } from "../components/user";
import { IconRefresh } from "../components/Icon";

function Buzz(props) {
  const {
    loading,
    error,
    data: contents,
    cacheTime,
    refresh,
    loadMore,
    hasMore,
    deleteCache,
  } = useCacheAndFetch({
    name: "Content",
    url: API_ROUTES.CONTENT,
  });

  // updateData } = useCacheAndFetch({ name: 'Event', url: 'http://192.168.5.68:3001/api/sharing' })
  const statusRecord = {
    pulling: "ဆွဲ",
    canRelease: "လွှတ်",
    refreshing: "refreshing...",
    complete: "loading",
  };

  const mylist = useMyList();
  return (
    <div className="p-2">
      <PullToRefresh
        onRefresh={() => {
          deleteCache({}, "Content");
          refresh();
        }}
        renderText={(status) => {
          return <div>{statusRecord[status]}</div>;
        }}
      >
        <center>{loading && <Loading />}</center>
        <div
          onClick={() => {
            deleteCache({}, "Content");
            refresh();
          }}
        >
          {error && error.message}
          {cacheTime && (
            <Space className="items-center cursor-pointer">
              updated {ago(cacheTime)}{" "}
              <IconRefresh/>
            </Space>
          )}
        </div>

        <ContentCreateButton />
        <div className="[&>*]:mb-8">
          {contents &&
            contents.map((buzz) => {
              var user = mylist.user.find((x) => x.id == buzz.created_by);
              return (
                <div key={buzz.id}>
                  {buzz.created_by && (
                    <UserBy user={user} time={buzz.create_time} />
                  )}
                  <ContentCard {...buzz} />
                </div>
              );
            })}
          <InfiniteScroll
            loadMore={loadMore}
            loading={loading}
            hasMore={hasMore}
          />
        </div>
      </PullToRefresh>
      <CommentPannel />
    </div>
  );
}

export default Buzz;
