import { useRouteError } from "react-router-dom";
import AppNavBar from "../components/appNavBar";
import { Card } from "antd-mobile";

export default function ErrorPage() {
  const error = useRouteError();
  console.error(error);

  return (
    <div>
      <div className="fixed right-0 top-0 left-0 bottom-0 bg-gradient-to-b from-white via-fuchsia-100 to-indigo-100 -z-10" />

      <div className="flex justify-center">
        <Card className="m-2 shadow-md">
          <h1>Oops!</h1>
          <p>Sorry, an unexpected error has occurred.</p>
          <p>
            {error && <i>{error.statusText || error.message}</i>}
          </p>
        </Card>
      </div>
      <div className="fixed right-0 left-0 bottom-0">
        <AppNavBar />
      </div>
    </div>
  );
}