import { AutoCenter, Avatar, Loading, Popup } from "antd-mobile";
import React, { useEffect, useRef, useState } from "react";
import { useAuth, useMyComponent } from "../hooks/context";
import { IcoSend } from "./Icon";
import { Button, Input } from "antd";
import { API_ROUTES } from "../const";
import { CommentItem } from "./leaf";
import useCacheAndFetch from "../hooks/useFetch";
import useForm from "../hooks/useForm";
import { UserAvatar } from "./user";

function CommentPannel() {
  const { commentContentId, setCommentContentId } = useMyComponent();
  const [currentId, setCurrentId] = useState(null);
  const { userInfo: user } = useAuth();
  const {
    loading,
    error,
    data: comments,
    addData,
    refresh,
    loadMore,
    hasMore,
    deleteCache,
  } = useCacheAndFetch({
    name: "Comment",
    url: currentId && API_ROUTES.COMMENT({ content_id: currentId }),
    age: 0,
  });
  const inputRef = useRef();
  useEffect(() => {
    if (commentContentId) {
      setCurrentId(commentContentId);
      const timeoutId = setTimeout(() => {
        inputRef.current.focus();
      }, 0);

      return () => clearTimeout(timeoutId);
    }
  }, [commentContentId]);

  const {
    loading: senting,
    error: sendingErr,
    setBody,
    body,
    submit,
    response: sendingResponse,
  } = useForm({
    target: API_ROUTES.COMMENT({ content_id: currentId }),
  });
  useEffect(() => {
    if (sendingResponse) {
      addData(sendingResponse);
    }
  }, [sendingResponse]);
  const handleSubmit = (event) => {
    event.preventDefault();
    submit();
  };
  return (
    <Popup
      // key={commentContentId}
      visible={commentContentId ? true : false}
      onMaskClick={() => {
        setCommentContentId(null);
      }}
      onClose={() => {
        setCommentContentId(null);
      }}
      bodyStyle={{ height: "80vh", overflow: "auto" }}
    >
      <div
        className="flex flex-col justify-between max-w-sm mx-auto"
        style={{ minHeight: "80vh" }}
      >
        <div>
          <center className="m-4">
            <h3>Comment</h3>
          </center>
          {loading && (
            <AutoCenter>
              <Loading />
            </AutoCenter>
          )}
          {comments &&
            comments.map((c) => {
              return <CommentItem key={c.id} {...c} />;
            })}
        </div>
        {user && (
          <form
            onSubmit={handleSubmit}
            className="flex gap-2 items-center p-2 sticky bottom-0"
          >
            <UserAvatar user={user} />
            <Input
              placeholder="ဇာပြောချင့်ပါလေ"
              className="rounded-full flex-1"
              value={body?.comment}
              ref={inputRef}
              onChange={(e) => setBody({ comment: e.target.value })}
            />

            <Button
              type={"submit"}
              shape="circle"
              icon={<IcoSend />}
              onClick={submit}
            />
          </form>
        )}
      </div>
    </Popup>
  );
}

export default CommentPannel;
