import React, { useState } from "react";
import useCacheAndFetch from "../hooks/useFetch";
import { JSONTree } from "react-json-tree";
import { Avatar, Card, List, Loading, PullToRefresh, Space } from "antd-mobile";
import moment from "moment/moment";
import {
  CheckCircleOutline,
  DeleteOutline,
  EditFill,
  HeartOutline,
  MessageOutline,
  RightOutline,
} from "antd-mobile-icons";
import EventCard, { EventListItem } from "../components/content/event";
import { useParams } from "react-router-dom";
import { API_ROUTES } from "../const";
import { useAuth, useMyList } from "../hooks/context";
import { UserCard } from "../components/user";
import ErrorPage from "./error-page";
import { Button, Image } from "antd";
import { ContentActionBar } from "../components/leaf";
import CommentPannel from "../components/CommentPannel";
import ContentFormPanel from "../components/ContentCreate";

function ContentDetail(props) {
  let { id } = useParams();
  const {
    loading,
    error,
    data: event,
    cacheTime,
    refresh,
    addData,
    removeData,
    // updateData } = useCacheAndFetch({ name: 'Event', url: 'http://192.168.20.80:10004/api/test' })
    // updateData } = useCacheAndFetch({ name: 'Event', url: 'https://api.heinsoe.com' })
    updateData,
  } = useCacheAndFetch({
    name: "ContentDetail",
    url: API_ROUTES.CONTENT_DETAIL({ id: id }),
  });
  // updateData } = useCacheAndFetch({ name: 'Event', url: 'http://192.168.5.68:3001/api/sharing' })

  const statusRecord = {
    pulling: "ဆွဲ",
    canRelease: "လွှတ်",
    refreshing: "refreshing...",
    complete: "loading",
  };

  const [noImg, setNoImg] = useState(false);
  const mylist = useMyList();
  const { userInfo } = useAuth();
  const isMine = event?.created_by == userInfo?.id;
  return error ? (
    <ErrorPage />
  ) : (
    <PullToRefresh
      onRefresh={() => {
        refresh();
      }}
      renderText={(status) => {
        return <div>{statusRecord[status]}</div>;
      }}
    >
      <center>{loading && <Loading />}</center>
      {/* {console.log(event)} */}
      {event && (
        <>
          {!noImg && (
            <Image width="100%" src={event.img_url} onError={setNoImg} />
          )}
          <div className="m-4">
            {event.fields.event_date && (
              <div className="ms-2 mb-2 float-right text-center p-2 rounded-md shadow-2xl bg-white">
                <div>{moment(event.fields.event_date).format("MMM")}</div>
                <div className="leading-none text-3xl font-bold">
                  {moment(event.fields.event_date).format("DD")}
                </div>
                <div className="text-xs opacity-60">
                  {moment(event.fields.event_date).format("YYYY")}
                </div>
                <hr className="my-2" />
                <div>{moment(event.fields.event_date).format("hh:MM a")}</div>
              </div>
            )}
            <div className="flex-1">
              <div className="flex justify-between">
                <div className="text-lg">{event.title}</div>
              </div>
              <i>{event.topics.map((t, i) => `#${t.name}`).join(", ")} </i>
              <div className="my-4">
                <h3>Description</h3>
                <p className="indent-4">{event.description}</p>
              </div>
            </div>
            {/* <JSONTree data={mylist}/> */}
            {event.fields.speaker_ids && (
              <div className="flex gap-3 my-2">
                {mylist.user
                  .filter((u) =>
                    event.fields.speaker_ids.split(",").includes(String(u.id))
                  )
                  .map((user) => {
                    return <UserCard user={user} key={user.id} />;
                  })}
              </div>
            )}
          </div>
          {isMine && <ActionBar content={event} refresh={refresh}  />}
          <ContentActionBar {...event} />

          <CommentPannel />
        </>
      )}
    </PullToRefresh>
  );
}

export default ContentDetail;

const ActionBar = ({ content, refresh }) => {
  const [visible, setVisible] = useState(false);
  return (
    <>
      <ContentFormPanel
        visible={visible}
        setVisible={setVisible}
        defaultData={content}
        refresh={refresh}
      />
      <div className="flex justify-around">
        <Button icon={<EditFill />} title="Edit" onClick={setVisible}></Button>
        <Button icon={<DeleteOutline />} title="Delete"></Button>
      </div>
    </>
  );
};
