import React from "react";
import { Outlet } from "react-router-dom";
import AppNavBar from "./components/appNavBar";

function Layout(props) {
  return (
    <div className="min-w">
      <div className="fixed right-0 top-0 left-0 bottom-0 bg-gradient-to-b from-white via-fuchsia-100 to-indigo-100 -z-10" />
      <div className="min-w pb-12">
        <Outlet />
      </div>
      <div className="fixed bottom-0 right-0 left-0 min-w">
        <AppNavBar />
      </div>
    </div>
  );
}

export default Layout;
