import React, { useEffect, useState } from "react";
import useCacheAndFetch from "../hooks/useFetch";
import { JSONTree } from "react-json-tree";
import {
  Avatar,
  Button,
  Card,
  Dialog,
  FloatingBubble,
  Image,
  List,
  Loading,
  Modal,
  PullToRefresh,
  Space,
} from "antd-mobile";
import moment from "moment/moment";
import {
  AddOutline,
  CalendarOutline,
  CheckCircleOutline,
  HeartOutline,
  MessageOutline,
  RightOutline,
} from "antd-mobile-icons";
import EventCard from "../components/content/event";
import { API_ROUTES, APP_ROUTES } from "../const";
import { useAuth } from "../hooks/context";
import { useNavigate } from "react-router-dom";
import InfiniteScroll from "../components/infiniteScroll";
import { ContentCreateButton, ago } from "../components/leaf";
import CommentPannel from "../components/CommentPannel";
import { IconRefresh } from "../components/Icon";

function Event(props) {
  const {
    loading,
    error,
    data: events,
    cacheTime,
    refresh,
    loadMore,
    hasMore,
    deleteCache,
  } = useCacheAndFetch({
    name: "Event",
    url: API_ROUTES.CONTENT_TYPE({ type_name: "event" }),
  });

  // updateData } = useCacheAndFetch({ name: 'Event', url: 'http://192.168.5.68:3001/api/sharing' })
  const { userInfo } = useAuth();
  const statusRecord = {
    pulling: "ဆွဲ",
    canRelease: "လွှတ်",
    refreshing: "refreshing...",
    complete: "loading",
  };
  const navigate = useNavigate();
  return (
    <div className="p-2">
      <PullToRefresh
        onRefresh={() => {
          deleteCache({}, "Event");
          refresh();
        }}
        renderText={(status) => {
          return <div>{statusRecord[status]}</div>;
        }}
      >
        <center>{loading && <Loading />}</center>
        <div
          onClick={() => {
            deleteCache({}, "Content");
            refresh();
          }}
        >
          {error && error.message}
          {cacheTime && (
            <Space className="items-center cursor-pointer">
              updated {ago(cacheTime)} <IconRefresh />
            </Space>
          )}
        </div>
        <div className="[&>*]:mb-8">
          {events &&
            events.map((event) => {
              return <EventCard key={event.id} {...event} />;
            })}
          <InfiniteScroll
            loadMore={loadMore}
            loading={loading}
            hasMore={hasMore}
          />
        </div>
      </PullToRefresh>
      <CommentPannel />
    </div>
  );
}

export default Event;
